import React, { Component } from 'react';
import Tutorial from './components/TutorialComponent';
import './App.css';
// import logo from './logo.svg';


class App extends Component {

  handleBackButtonClick(e) {
    const tag = document.querySelector('.App')
    // console.log(e);
    // console.log(tag)
    // window.location.hash = "#App" // nie działa
    tag.scrollIntoView();
  }

  render(){
    return (
      <div className="App" id="App">

        <button className="BackButton" onClick={this.handleBackButtonClick}>↑</button>

        <div className="Cover">
          <h1>Jaki laptop kupić?</h1>
          <p>Poradnik kupna laptopa</p>
        </div>

        <Tutorial/>

      </div>)
    }
}
